body {
  color: #121212;
  background-color: #ffffff;
  font-family: "Roboto", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

.container {
  margin: 0 auto;
  padding: 96px 15px;
  width: 320px;
}
@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}
@media screen and (min-width: 1440px) {
  .container {
    width: 1440px;
  }
}

.card {
  display: flex;
}

.card__image {
  margin-right: 95px;
}

.card__wrapper {
  width: 380px;
}

.card__title {
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  color: #121212;
  margin-bottom: 24px;
}

.card__description {
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  color: #121212;
  margin-bottom: 32px;
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(18, 18, 18, 0.5);
  opacity: 1;
  transition: opacity 250ms linear;
}
.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.modal {
  position: relative;
  top: 50%;
  left: 50%;
  width: 920px;
  padding: 56px 95px;
  background-color: #ffffff;
  transform: translate(-50%, -50%);
  overflow-y: auto;
}

.modal__button {
  position: absolute;
  top: 40px;
  right: 40px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.modal__icon {
  width: 24px;
  height: 24px;
  stroke: #121212;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: inherit;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  color: #ffffff;
  background-color: #e4c1ae;
  border: none;
  border-radius: 32px;
  padding: 16px 40px;
  cursor: pointer;
  transition: color 250ms linear;
}
.button:hover, .button:focus {
  color: #121212;
}

.alert__title {
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  color: #121212;
  margin-bottom: 24px;
}

.alert__text {
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  color: #121212;
}

.alert__value {
  font-weight: 600;
}